import React, { useEffect, useRef } from "react";
import cytoscape from "cytoscape";
import { option } from "../assets/option";
import QueueSim from "./QueueSim";
import Draggable from "react-draggable";
import DragIcon from "../assets/drag-indicator.svg";
import cxtmenu from "cytoscape-cxtmenu";
import { loadCSS } from "fg-loadcss";
import EditModal from "../EditModal";
import DataStructureInfo from "./DataStructureInfo";
import ComparisonPanel from "./ComparisonPanel";

cytoscape.use(cxtmenu);

const TBatchAction = {
  name: String,
  param: {
    group: "nodes" | "edges",
    data: {
      id: String,
      source: String,
      target: String,
    },
    renderedPosition: {
      x: Number,
      y: Number,
    },
  },
};

const randomNumber = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1)) + min;
};

export const generateEdges = (graphData) => {
  option.elements.edges = [];

  for (let i = 0; i < graphData.noOfNodes + randomNumber(0, 4); i++) {
    const a = randomNumber(1, graphData.noOfNodes);
    const b = randomNumber(1, graphData.noOfNodes);
    if (
      a !== b &&
      !option.elements.edges.some((edge) => {
        return edge.data.source === a && edge.data.target === b;
      })
    ) {
      option.elements.edges = [
        ...option.elements.edges,
        {
          data: {
            id: `${a}${b}`,
            weight: randomNumber(0, graphData.noOfNodes + 4),
            source: `${a}`,
            target: `${b}`,
          },
        },
      ];
    }
  }
};

const GraphSim = ({ graphData, setGraphData }) => {
  const graphRef = useRef(null);
  const [log, setLog] = React.useState([]);
  const [edge, setEdge] = React.useState([]);
  const [queue, setQueue] = React.useState([]);
  const [statusMessage, setStatusMessage] = React.useState({
    action: null,
    node: null,
  });
  const [prevGraphData, setPrevGraphData] = React.useState({
    ...graphData,
    noOfNodes: 3,
  });
  const [adjacencyList, setAdjacencyList] = React.useState({});
  const [traversalOrder, setTraversalOrder] = React.useState([]);
  const [showComparison, setShowComparison] = React.useState(false);
  const [cy, setCy] = React.useState(null);
  const [containerLayout, setContainerLayout] = React.useState("desktop"); // 'desktop', 'tablet', 'mobile'

  const [openDialog, setOpenDialog] = React.useState({
    openLearn: null,
    openAdd: null,
  });

  const handleOpenLearn = (ele) => {
    const data = ele.data();
    setOpenDialog({ ...openDialog, openLearn: true, nodeObj: data });
  };

  const urRef = React.useRef(null);

  const generateNodes = () => {
    option.elements.nodes = [{ data: { id: "1", label: "Node 1" } }];

    if (
      graphData.noOfNodes > 1 &&
      option.elements.nodes.length < graphData.noOfNodes
    )
      for (let i = 2; i <= graphData.noOfNodes; i++)
        option.elements.nodes = [
          ...option.elements.nodes,
          { data: { id: `${i}`, label: `Node ${i}` } },
        ];

    setGraphData({ ...graphData, flag: 1 });
  };

  const divRef = useRef(null);

  useEffect(() => {
    divRef?.current?.scrollIntoView({ behavior: "smooth" });
  });

  // Handle responsive layout
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth < 768) {
        setContainerLayout("mobile");
      } else if (window.innerWidth < 1024) {
        setContainerLayout("tablet");
      } else {
        setContainerLayout("desktop");
      }
    };

    // Initial call
    handleResize();

    // Add event listener
    window.addEventListener("resize", handleResize);

    // Clean up
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  useEffect(() => {
    if (prevGraphData.noOfNodes !== graphData.noOfNodes) {
      generateNodes();
      setEdge([]);
    }

    if (graphData.flag !== 3 && graphData.flag !== 5 && !graphData.randomize)
      option.elements.edges = [];

    if (graphData.randomize && prevGraphData.noOfNodes !== graphData.noOfNodes)
      generateEdges(graphData);

    drawGraph();

    setPrevGraphData(graphData);
  }, [graphData]);

  useEffect(() => {
    if (edge.length === 2) {
      option.elements.edges = [
        ...option.elements.edges,
        {
          data: {
            id: `${edge[0]}${edge[1]}`,
            weight: randomNumber(0, graphData.noOfNodes + 4),
            source: `${edge[0]}`,
            target: `${edge[1]}`,
          },
        },
      ];
      drawGraph();
      setEdge([]);
      setGraphData({ ...graphData, flag: 5 });
    }
  }, [edge]);

  const [editModalContent, setEditModalContent] = React.useState();

  const buildAdjacencyList = (cy) => {
    const list = {};
    cy.nodes().forEach((node) => {
      list[node.id()] = [];
    });

    cy.edges().forEach((edge) => {
      const source = edge.data("source");
      const target = edge.data("target");
      if (!list[source].includes(target)) {
        list[source].push(target);
      }
    });

    setAdjacencyList(list);
    return list;
  };

  // Enhanced BFS animation function with edge highlighting
  const runBFSAnimation = (cyInstance, startNode) => {
    setStatusMessage({ action: null, node: null });
    setTraversalOrder([]);
    setQueue([]);
    setLog([]);

    // Reset all nodes and edges
    cyInstance.nodes().removeClass("highlighted visited");
    cyInstance.edges().removeClass("highlighted-path"); // Reset edge highlighting

    // Build adjacency list
    const adjList = buildAdjacencyList(cyInstance);

    // Initial log entry
    setLog((prev) => [
      ...prev,
      {
        message: `Starting BFS from Node ${startNode}`,
        type: "INFO",
        step: 0,
      },
    ]);

    // Perform BFS to get the traversal order and parent relationships
    const visited = new Set();
    const order = [];
    const parent = {}; // Track parent-child relationships for edge highlighting
    const queue = [startNode];
    visited.add(startNode);
    parent[startNode] = null; // Start node has no parent

    let step = 1;
    setLog((prev) => [
      ...prev,
      {
        message: `Added Node ${startNode} to queue`,
        type: "QUEUE_ADD",
        step: step++,
      },
    ]);

    while (queue.length > 0) {
      const currentNode = queue.shift();
      order.push(currentNode);

      setLog((prev) => [
        ...prev,
        {
          message: `Processing Node ${currentNode} (dequeued)`,
          type: "PROCESSING",
          step: step++,
        },
      ]);

      const neighbors = adjList[currentNode] || [];

      setLog((prev) => [
        ...prev,
        {
          message: `Node ${currentNode} has neighbors: [${
            neighbors.join(", ") || "none"
          }]`,
          type: "NEIGHBORS",
          step: step++,
        },
      ]);

      for (const neighbor of neighbors) {
        if (!visited.has(neighbor)) {
          visited.add(neighbor);
          queue.push(neighbor);
          parent[neighbor] = currentNode; // Record the parent-child relationship

          setLog((prev) => [
            ...prev,
            {
              message: `Discovered Node ${neighbor} via edge from ${currentNode}`,
              type: "DISCOVERY",
              step: step++,
            },
          ]);
        } else {
          setLog((prev) => [
            ...prev,
            {
              message: `Node ${neighbor} already visited, skipping`,
              type: "SKIP",
              step: step++,
            },
          ]);
        }
      }

      setLog((prev) => [
        ...prev,
        {
          message: `Completed processing Node ${currentNode}`,
          type: "COMPLETED_NODE",
          step: step++,
        },
      ]);
    }

    // Animate the BFS traversal with edge highlighting
    let currentIndex = 0;
    const animationQueue = [];
    const highlightedEdges = new Set(); // Track highlighted edges

    const animateNext = () => {
      if (currentIndex < order.length) {
        const currentNode = order[currentIndex];

        // Highlight the current node
        cyInstance.$(`#${currentNode}`).addClass("highlighted visited");

        // Highlight the edge that connects to the parent (if exists)
        if (parent[currentNode]) {
          const edgeId = `${parent[currentNode]}${currentNode}`;
          const reverseEdgeId = `${currentNode}${parent[currentNode]}`;

          // Try both directions since edges can be directed or undirected
          const edge =
            cyInstance.$(`#${edgeId}`) || cyInstance.$(`#${reverseEdgeId}`);

          if (edge.length > 0) {
            edge.addClass("highlighted-path");
            highlightedEdges.add(edgeId);

            setLog((prev) => [
              ...prev,
              {
                message: `Highlighting edge ${parent[currentNode]}  ${currentNode}`,
                type: "EDGE_HIGHLIGHT",
                step: step++,
              },
            ]);
          }
        }

        // Update state
        setTraversalOrder((prev) => [...prev, currentNode]);
        setStatusMessage({ action: "VISITING", node: currentNode });

        // Add to animation queue for visualization
        if (currentIndex > 0) {
          animationQueue.push({
            node: order[currentIndex - 1],
            state: "VISITED",
          });
        }
        animationQueue.push({ node: currentNode, state: "PROCESSING" });
        setQueue(
          animationQueue.map((item) => ({ node: item.node, state: item.state }))
        );

        currentIndex++;
        setTimeout(animateNext, 1000);
      } else {
        // Animation complete
        setStatusMessage({ action: "COMPLETE", node: null });
        setLog((prev) => [
          ...prev,
          {
            message: "BFS Traversal Complete!",
            type: "COMPLETE",
            step: step,
          },
        ]);

        // Log the BFS tree edges
        const bfsTreeEdges = [];
        for (const node in parent) {
          if (parent[node]) {
            bfsTreeEdges.push(`${parent[node]}  ${node}`);
          }
        }

        setLog((prev) => [
          ...prev,
          {
            message: `BFS Tree Edges: ${bfsTreeEdges.join(", ")}`,
            type: "INFO",
            step: step + 1,
          },
        ]);
      }
    };

    animateNext();
  };

  const drawGraph = () => {
    const positions = {};
    let usePreset = false;

    // If cy instance exists and number of nodes is the same, save node positions
    if (cy && prevGraphData.noOfNodes === graphData.noOfNodes) {
      cy.nodes().forEach((node) => {
        positions[node.id()] = node.position();
      });
      if (Object.keys(positions).length > 0) {
        usePreset = true;
      }
    }

    if (usePreset) {
      option.elements.nodes.forEach((node) => {
        if (positions[node.data.id]) {
          node.position = positions[node.data.id];
        }
      });
      option.layout = { name: "preset" };
    } else {
      option.layout = {
        name: "breadthfirst",
        directed: true,
        roots: "#1",
        padding: 10,
      };
    }

    // Sanitize edges before drawing
    const existingNodeIds = option.elements.nodes.map((n) => n.data.id);
    option.elements.edges = option.elements.edges.filter((edge) => {
      return (
        existingNodeIds.includes(edge.data.source) &&
        existingNodeIds.includes(edge.data.target)
      );
    });

    option.container = graphRef.current;
    const cyInstance = cytoscape(option);
    setCy(cyInstance);

    // clean up position property after use
    if (usePreset) {
      option.elements.nodes.forEach((node) => {
        delete node.position;
      });
    }

    // Add custom styles for highlighted path
    cyInstance
      .style()
      .selector(".highlighted-path")
      .style({
        "line-color": "#2196F3",
        "target-arrow-color": "#2196F3",
        width: 3,
        "z-index": 999,
      })
      .update();

    if (!usePreset) {
      cyInstance.fit();
    }

    const defaultsLevel = {
      menuRadius: function (ele) {
        return 70;
      },
      selector: "node",
      commands: [
        {
          content: '<span class="fas fa-pen fa-2x"></span>',
          select: (node) => {
            setEditModalContent(node);
          },
          enabled: true,
        },
      ],

      fillColor: "rgba(0, 0, 0, 0.75)",
      activeFillColor: "rgba(1, 105, 217, 0.75)",
      activePadding: 8,
      indicatorSize: 24,
      separatorWidth: 3,
      spotlightPadding: 8,
      adaptativeNodeSpotlightRadius: true,
      openMenuEvents: "cxttapstart taphold",
      itemColor: "white",
      itemTextShadowColor: "transparent",
      zIndex: 9999,
      atMouse: false,
      outsideMenuCancel: false,
    };

    cyInstance.cxtmenu(defaultsLevel);

    cyInstance.on("tap", "node", function (evt) {
      cyInstance.$(`#${evt.target.id()}`).toggleClass("selected");
      setEdge((prev) => [...prev, evt.target.id()]);
    });

    // Run BFS animation when flag is set to 3
    if (graphData.flag === 3) {
      // Use setTimeout to ensure the graph is fully rendered before starting animation
      setTimeout(() => {
        runBFSAnimation(cyInstance, graphData.startingNode.toString());
      }, 100);
    }
  };

  urRef.current?.action(
    "changeData",
    ({ node, label }) => {
      const removeActions = [];
      removeActions.push({
        name: "remove",
        param: node,
      });
      removeActions.push({
        name: "remove",
        param: node.connectedEdges(),
      });
      const addActions = [];
      addActions.push({
        name: "add",
        param: {
          group: "nodes",
          data: { id: node.id(), label: label },
          position: node.position(),
        },
      });
      node.connectedEdges().forEach((edge) => {
        addActions.push({
          name: "add",
          param: {
            group: "edges",
            data: {
              id: edge.id(),
              source: edge.source().id(),
              target: edge.target().id(),
            },
          },
        });
      });
      urRef.current.do("batch", [...removeActions, ...addActions]);
    },
    () => urRef.current.undo()
  );

  const handleUpdateTitleOrType = React.useCallback((node, newLabel) => {
    urRef.current.do("changeData", {
      node,
      newLabel,
    });
  }, []);

  useEffect(() => {
    const fa = loadCSS(
      "https://use.fontawesome.com/releases/v5.12.0/css/all.css",
      document.querySelector("#font-awesome-css")
    );
    return () => {
      fa.parentNode.removeChild(fa);
    };
  }, []);

  // Helper function to get log message color based on type
  const getLogColor = (type) => {
    switch (type) {
      case "INFO":
        return "text-blue-400";
      case "QUEUE_ADD":
        return "text-green-400";
      case "PROCESSING":
        return "text-yellow-400";
      case "NEIGHBORS":
        return "text-purple-400";
      case "DISCOVERY":
        return "text-green-400";
      case "SKIP":
        return "text-gray-400";
      case "COMPLETED_NODE":
        return "text-orange-400";
      case "EDGE_HIGHLIGHT":
        return "text-cyan-400";
      case "COMPLETE":
        return "text-green-500 font-bold";
      default:
        return "text-white";
    }
  };

  return (
    <div className="flex flex-col h-screen w-full overflow-hidden">
      {/* Main content area with 3 containers */}
      <div
        className={`flex flex-1 overflow-hidden ${
          containerLayout === "mobile" ? "flex-col" : "flex-row"
        }`}
      >
        {/* Graph Container */}
        <div
          className={`
          ${containerLayout === "desktop" ? "w-1/2" : ""}
          ${containerLayout === "tablet" ? "w-3/5" : ""}
          ${containerLayout === "mobile" ? "w-full h-1/2" : ""}
          relative border-r border-gray-700
        `}
        >
          {/* <div
  style={{
    position: "absolute",
    top: 0,
    left: 0,
    padding: "16px 20px",
    fontSize: "16px",
    fontWeight: 500,
    color: "#ffffff",
    background: "rgba(31, 41, 55, 0.8)",
    borderBottomRightRadius: "12px",
    zIndex: 10,
  }}
>
 Click and drag nodes to reposition
</div> */}

          <div ref={graphRef} className="w-full h-full" />
        </div>

        {/* Right side containers (Queue and Data Structure Info) */}
        <div
          className={`
          flex flex-col
          ${containerLayout === "desktop" ? "w-1/2" : ""}
          ${containerLayout === "tablet" ? "w-2/5" : ""}
          ${containerLayout === "mobile" ? "w-full h-1/2" : ""}
          relative // Added relative here to contain absolute positioned child
        `}
        >
          <div
            style={{
              position: "absolute",
              top: 0,
              left: 0,
              padding: "16px 20px",
              fontSize: "16px",
              fontWeight: 500,
              color: "#ffffff",
              background: "rgba(31, 41, 55, 0.8)",
              borderBottomRightRadius: "12px",
              zIndex: 10,
            }}
          >
            Click and drag nodes to reposition
          </div>
          {/* Queue Container */}
          <div
            className={`
            ${containerLayout === "mobile" ? "h-1/2" : "h-1/2"}
            border-b border-gray-700
          `}
          >
            <QueueSim queue={queue} current={statusMessage.node} />
          </div>

          {/* Data Structure Info Container */}
          <div
            className={`
            ${containerLayout === "mobile" ? "h-1/2" : "h-1/2"}
            overflow-auto
          `}
          >
            <DataStructureInfo
              adjacencyList={adjacencyList}
              queue={queue}
              traversalOrder={traversalOrder}
              current={statusMessage.node}
            />
          </div>
        </div>
      </div>

      {statusMessage.action !== null && (
        <Draggable handle="#handle">
          <div
            className={`
        fixed rounded font-mono bg-gray-800 shadow text-white overflow-auto z-10
        max-h-[calc(100vh-120px)]
      `}
            style={{
              width: "500px",
              height: "350px",
              left: "910px",
              transform: "translateX(-50%)",
              bottom: "25px",
            }}
          >
            <div className="text-lg font-bold mb-3 border-b border-gray-600 pb-2 px-5 pt-5">
              BFS Traversal Log
            </div>
            <div className="space-y-1 text-sm px-5 pb-5">
              {log.map((item, index) => (
                <div
                  key={index}
                  className={`${getLogColor(item.type)} flex items-start`}
                >
                  <span className="text-gray-500 mr-2 text-xs min-w-[30px]">
                    [{item.step}]
                  </span>
                  <span className="break-words">{item.message}</span>
                </div>
              ))}
            </div>

            {statusMessage.action === "COMPLETE" && (
              <div className="mt-8 pt-3 border-t border-gray-600 px-5 pb-5">
                <div className="font-bold mb-2">Summary:</div>
                <div>Traversal Path: {traversalOrder.join("  ")}</div>
                <div>
                  Time Complexity: O(V + E) ={" "}
                  {traversalOrder.length +
                    Object.values(adjacencyList).flat().length}
                </div>
                <div>Space Complexity: O(V) = {traversalOrder.length}</div>
                <button
                  className="mt-20 bg-blue-500 hover:bg-blue-700 text-white font-bold py-3 px-6 rounded text-xs"
                  onClick={() => setShowComparison(true)}
                >
                  Compare with DFS
                </button>
              </div>
            )}
            <div ref={divRef} id="handle" className="pt-1">
              <div className="bg-gray-600 w-full items-center justify-center flex cursor-move">
                <img
                  src={DragIcon}
                  style={{
                    width: "20px",
                    height: "20px",
                    pointerEvents: "none",
                  }}
                  alt="random"
                  className="rotate-90 transform"
                />
              </div>
            </div>
          </div>
        </Draggable>
      )}
      <EditModal
        editModalContent={editModalContent}
        setEditModalContent={setEditModalContent}
        handleConfirmButtonClick={handleUpdateTitleOrType}
      />
      <ComparisonPanel
        open={showComparison}
        onClose={() => setShowComparison(false)}
      />

      {/* Add custom styles for highlighted path */}
      <style>
        {`
          .highlighted-path {
            line-color: #2196F3 !important;
            target-arrow-color: #2196F3 !important;
            width: 3px !important;
            z-index: 999 !important;
            transition: all 0.3s ease !important;
          }
        `}
      </style>
    </div>
  );
};

export default GraphSim;
