import React from "react";

const ComparisonPanel = ({ open, onClose }) => {
  if (!open) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-gray-800 text-white p-6 rounded-lg shadow-xl max-w-2xl w-full mx-4">
        <h2 className="text-2xl font-bold mb-4">BFS vs DFS Comparison</h2>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
          <div className="bg-gray-700 p-4 rounded">
            <h3 className="text-lg font-semibold mb-2 text-blue-400">
              Breadth-First Search (BFS)
            </h3>
            <ul className="list-disc pl-5 space-y-1">
              <li>
                Uses <strong>Queue</strong> data structure (FIFO)
              </li>
              <li>Explores all neighbors first (level by level)</li>
              <li>Finds shortest path in unweighted graphs</li>
              <li>Time Complexity: O(V + E)</li>
              <li>Space Complexity: O(V)</li>
              <li>Better when target is close to source</li>
            </ul>
          </div>

          <div className="bg-gray-700 p-4 rounded">
            <h3 className="text-lg font-semibold mb-2 text-green-400">
              Depth-First Search (DFS)
            </h3>
            <ul className="list-disc pl-5 space-y-1">
              <li>
                Uses <strong>Stack</strong> data structure (LIFO) or recursion
              </li>
              <li>Explores as far as possible along each branch</li>
              <li>Better for topological sorting, cycle detection</li>
              <li>Time Complexity: O(V + E)</li>
              <li>Space Complexity: O(V) (better for deep graphs)</li>
              <li>Better when graph is deep but narrow</li>
            </ul>
          </div>
        </div>

        <div className="bg-gray-700 p-4 rounded mb-4">
          <h3 className="text-lg font-semibold mb-2">
            Visualization Differences
          </h3>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <h4 className="font-semibold text-blue-400">BFS Visualization</h4>
              <ul className="list-disc pl-5">
                <li>Nodes visited level by level</li>
                <li>Queue shows the order of exploration</li>
                <li>Traversal order: 1, 2, 3, 4, 5, ...</li>
              </ul>
            </div>
            <div>
              <h4 className="font-semibold text-green-400">
                DFS Visualization
              </h4>
              <ul className="list-disc pl-5">
                <li>Nodes visited along a path until dead end</li>
                <li>Stack shows the current path</li>
                <li>Traversal order: 1, 5, 9, 10, 6, ...</li>
              </ul>
            </div>
          </div>
        </div>

        <div className="flex justify-end">
          <button
            className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded transition-colors"
            onClick={onClose}
          >
            Close Comparison
          </button>
        </div>
      </div>
    </div>
  );
};

export default ComparisonPanel;
