import React from 'react';
import Initializer from "./components/Initializer";
import GraphSim from "./components/GraphSim";
import { createTheme, ThemeProvider } from "@material-ui/core/styles";

const App = () => {
  const [graphData, setGraphData] = React.useState({
    startingNode: 1,
    noOfNodes: 5,
    randomize: true,
    flag: 1,
  });
  const theme = createTheme();

  React.useEffect(() => {
    if (graphData.startingNode > graphData.noOfNodes) {
      setGraphData((prev) => ({ ...prev, startingNode: 1 }));
    }
  }, [graphData.noOfNodes]);

  return (
    <ThemeProvider theme={theme}>
      <div
        className="flex justify-between bg-gray-800/90  items-center"
        style={{ width: "100vw", height: "100vh" }}
      >
        <Initializer graphData={graphData} setGraphData={setGraphData} />
        <GraphSim graphData={graphData} setGraphData={setGraphData} />
      </div>
    </ThemeProvider>
  );
};

export default App
