import React from "react";

const DataStructureInfo = ({
  adjacencyList,
  queue,
  traversalOrder,
  current,
}) => {
  // Calculate the current queue size (only nodes that are enqueued)
  const currentQueueSize = queue.filter(
    (item) => item.state === "ENQUEUED"
  ).length;

  // Calculate the maximum queue size during traversal
  const maxQueueSize = Math.max(
    ...queue.reduce(
      (sizes, item) => {
        if (item.state === "ENQUEUED") {
          sizes.push(sizes[sizes.length - 1] + 1);
        } else if (item.state === "DEQUEUED") {
          sizes.push(sizes[sizes.length - 1] - 1);
        }
        return sizes;
      },
      [0]
    )
  );

  return (
    <div
      className="absolute right-19 top-1  mt-20 ml-5 bg-gray-800/90 text-white p-4   overflow-y-auto"
      style={{ width: "500px", maxHeight: "calc(56vh - 150px)" }}
    >
      <h3 className="text-lg font-bold mb-3 border-b border-gray-600 pb-2 px-5 pt-5">
        Data Structures
      </h3>

      {/* Adjacency List Section */}
      <div className="mb-4">
        <h4 className="font-semibold mb-2 text-blue-300">
          Adjacency List Representation
        </h4>
        <div className="bg-gray-700 p-3 rounded max-h-40 overflow-y-auto">
          {Object.keys(adjacencyList).length > 0 ? (
            Object.entries(adjacencyList).map(([node, neighbors]) => (
              <div key={node} className="mb-2 font-mono text-sm">
                <span className="text-yellow-300">{node}:</span>
                <span className="ml-2">
                  [
                  {neighbors.length > 0
                    ? neighbors.join(", ")
                    : "No connections"}
                  ]
                </span>
              </div>
            ))
          ) : (
            <div className="text-gray-400 text-sm">No graph data available</div>
          )}
        </div>
        <div className="text-xs text-gray-400 mt-1">
          Represents graph connections between nodes
        </div>
      </div>

      {/* Queue Operations Section */}
      <div className="mb-4">
        <h4 className="font-semibold mb-2 text-green-300">
          Queue Operations (BFS)
        </h4>
        <div className="bg-gray-700 p-3 rounded">
          <div className="grid grid-cols-2 gap-2 text-sm">
            <div className="text-cyan-300">Enqueue:</div>
            <div>Add node to the end (FIFO)</div>

            <div className="text-cyan-300">Dequeue:</div>
            <div>Remove node from the front</div>
          </div>
        </div>
        <div className="text-xs text-gray-400 mt-1">
          BFS uses Queue (First-In-First-Out) structure
        </div>
      </div>

      {/* Traversal Order Section */}
      {traversalOrder.length > 0 && (
        <div className="mb-4">
          <h4 className="font-semibold mb-2 text-purple-300">
            Traversal Order
          </h4>
          <div className="bg-gray-700 p-3 rounded">
            <div className="flex flex-wrap gap-1">
              {traversalOrder.map((node, index) => (
                <span
                  key={index}
                  className={`px-2 py-1 rounded text-sm ${
                    node === current
                      ? "bg-blue-600 text-white font-bold"
                      : "bg-gray-600 text-gray-200"
                  }`}
                >
                  {node}
                  {index < traversalOrder.length - 1 && (
                    <span className="text-gray-400 ml-1"></span>
                  )}
                </span>
              ))}
            </div>
          </div>
          <div className="text-xs text-gray-400 mt-1">
            Order of nodes visited in BFS traversal
          </div>
        </div>
      )}

      {/* Current Node Section */}
      {current && (
        <div className="mb-4">
          <h4 className="font-semibold mb-2 text-orange-300">Current Node</h4>
          <div className="bg-blue-700 p-3 rounded text-center font-bold text-lg">
            {current}
          </div>
          <div className="text-xs text-gray-400 mt-1">
            Currently being processed
          </div>
        </div>
      )}

      {/* Algorithm Complexity Section */}
      <div className="mt-4 pt-3 border-t border-gray-600">
        <h4 className="font-semibold mb-2 text-red-300">
          Algorithm Complexity
        </h4>
        <div className="bg-gray-700 p-3 rounded text-sm">
          <div className="grid grid-cols-2 gap-2">
            <div className="text-cyan-300">Time:</div>
            <div>
              O(V + E) = O({Object.keys(adjacencyList).length} +{" "}
              {Object.values(adjacencyList).flat().length})
            </div>

            <div className="text-cyan-300">Space:</div>
            <div>O(V) = O({maxQueueSize})</div>

            <div className="text-cyan-300">Nodes (V):</div>
            <div>{Object.keys(adjacencyList).length}</div>

            <div className="text-cyan-300">Edges (E):</div>
            <div>{Object.values(adjacencyList).flat().length}</div>
          </div>
        </div>
      </div>

      {/* BFS Characteristics */}
      <div className="mt-4 pt-3 border-t border-gray-600">
        <h4 className="font-semibold mb-2 text-yellow-300">
          BFS Characteristics
        </h4>
        <ul className="text-sm text-gray-300 space-y-1">
          <li> Level-order traversal</li>
          <li> Finds shortest path (unweighted graphs)</li>
          <li> Uses Queue data structure</li>
          <li> Complete and optimal for unweighted graphs</li>
        </ul>
      </div>
    </div>
  );
};

export default DataStructureInfo;
